import React from 'react';
import { Formik } from 'formik';
import * as Yup from 'yup';
const model = 'comment';

const FieldsSchema = Yup.object().shape({
    
    text: Yup.string()
        .min(2, 'Must be 2 characters or more!')
        .max(350, 'Too Long!')
        .required('Required'),
    
});

export default function Form({item, afterSubmit, children}) {

    item = item.id !== 'new' ? item : { like: { owner: '', value: 0 }, id: 'new', text: '',  }
    
    // items gen
	if(Array.isArray(item.like)) item.like.forEach(f => delete f.__typename);

    return <Formik
        initialValues={item}
        enableReinitialize={true}
        validationSchema={FieldsSchema}
        onSubmit={(values, actions) => {
            actions.setSubmitting(false);
            //actions.resetForm();
            const e = { target: { id: model, value: values } };
            afterSubmit(e);
        }}
    >
        {props => children({ ...props })}
    </Formik>;
}