
import React from 'react';
import { TextField, Grid, Container, ListItemText } from '@material-ui/core';
import CreateMany from '../../components/hasMany/createMany';
import { useTheme } from '@material-ui/core/styles';
import { GETS_WHERE, UPDATE, DELETE } from '../../queries/comments.js';
import Edit from '../comments/edit';
import Create from '../comments/create';

export default function EditView1(props) {
    const theme = useTheme();

    return <Container>
        <Grid container spacing={2} justify="center" alignItems="center">
            <Grid item xs={12} sm={12} md={12} >
                <TextField
                    name="name"
                    label="name"
                    variant="outlined"
                    margin="normal"
                    required
                    fullWidth
                    value={props.values.name}
                    error={props.errors.name && props.touched.name ? true : false}
                    helperText={props.errors.name && props.touched.name ? props.errors.name : null}
                    onBlur={e => { props.handleChange(e); props.submitForm(); }}
                    onChange={props.handleChange}
                />
            </Grid>
            <Grid item xs={12} sm={12} md={12} >
                <CreateMany
                    name={'comments'}
                    viewType='list' // can be grid list table supertable swipe raw
                    superTableOptions={{}}
                    query_where={GETS_WHERE}
                    query_variables={{ ids: props.values.comments }}
                    query_update={UPDATE}
                    query_delete={DELETE}
                    onChange={e => { props.handleChange(e); props.submitForm(); }}
                    withUrl={false} // use url
                    EditForm={(Edit)}
                    CreateForm={Create}
                    headers={{}}
                    elementContent={(item, index) => {

                        // list
                        return <React.Fragment>
                            <ListItemText
                                primary={item.text}
                                secondary={item.text} />
                        </React.Fragment>

                    }}
                    cardActions={(item, index) => null}
                    cardCollapse={(item, index) => null}
                    dialogName=''
                    actionType='create' // create or create-default
                    addButtonType='button' // can be fab button inline none
                    addButtonName={theme.props.components.Add}
                    editButtonName={theme.props.components.Edit}
                    deleteButtonName={theme.props.components.Delete}
                    deleteButton={(item, i) => 'each'} // can be 'each', 'last', 'none'
                    editButton={(item, i) => 'each'} // can be 'each', 'last', 'none'
                />
            </Grid>
            <Grid item xs={12} sm={12} md={12} >
                <TextField
                    name="cover"
                    label="cover"
                    variant="outlined"
                    margin="normal"
                    required
                    fullWidth
                    value={props.values.cover}
                    error={props.errors.cover && props.touched.cover ? true : false}
                    helperText={props.errors.cover && props.touched.cover ? props.errors.cover : null}
                    onBlur={e => { props.handleChange(e); props.submitForm(); }}
                    onChange={props.handleChange}
                />
            </Grid>

        </Grid>
    </Container>
}