
import React from 'react';
import { TextField, Grid, Container } from '@material-ui/core';
//import { useTheme } from '@material-ui/core/styles';

export default function EditView1(props) {
    // const theme = useTheme();
    return <Container>
        <Grid container spacing={2} justify="center" alignItems="center">
            <Grid item xs={12} sm={12} md={12} >
                <TextField
                    name="text"
                    label="text"
                    variant="outlined"
                    margin="normal"
                    required
                    fullWidth
                    value={props.values.text}
                    error={props.errors.text && props.touched.text ? true : false}
                    helperText={props.errors.text && props.touched.text ? props.errors.text : null}
                    onBlur={e => { props.handleChange(e); props.submitForm(); }}
                    onChange={props.handleChange}
                />
            </Grid>
            
        </Grid>
    </Container>
}